import React, { Component } from "react";
import SpecCompareTable from "./SpecCompareTable";
import Accordion from "./Accordion";
import ProductCompareWithDropDown from "./ProductCompareWithDropDown";
//import { toJS } from "mobx";
import { Observer } from "mobx-react";

export default class ProductComparePage extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <Observer>
        {() => {
          //console.log("ComparePage props: ", toJS(this.props.data));
          if(this.props.data && this.props.data.showDropdownsLeft && this.props.data.showDropdownsRight && this.props.data.compareAccordion && this.props.data.compareAccordion.currentAccOpen>=0){
            if(typeof(this.props.changeAccIndex)!=undefined){
              this.props.changeAccIndex(-1);
            }
          }

          return (
            <>
              <ProductCompareWithDropDown
                showDropdownsLeft={this.props.data.showDropdownsLeft}
                showDropdownsRight={this.props.data.showDropdownsRight}
                itemSelectedLeft={this.props.data.itemSelectedLeft}
                itemSelectedRight={this.props.data.itemSelectedRight}
                onCloseThumbnailLeft={this.props.onCloseThumbnailLeft}
                onCloseThumbnailRight={this.props.onCloseThumbnailRight}
                brands={this.props.data.brands}
                modelLeft={this.props.data.modelLeft}
                modelRight={this.props.data.modelRight}
                product={this.props.data.product}
                onBrandSelected={this.props.onBrandSelected}
                onModelSelected={this.props.onModelSelected}
              />

              <Accordion
                itemLabels={this.props.data.compareAccordion.itemLabels}
                currentAccOpen={this.props.data.compareAccordion.currentAccOpen}
                changeAccIndex={this.props.changeAccIndex}
                showDropdownsLeft={this.props.data.showDropdownsLeft}
                showDropdownsRight={this.props.data.showDropdownsRight}
                accordainWrpClassName={this.props.data.compareAccordion.accordainWrpClassName}

              >
                {this.props.data.compareAccordion.children &&
                  this.props.data.compareAccordion.children.map((el) => (
                    <SpecCompareTable
                      rows={el.attributes.rows}
                      showTableLeft={!this.props.data.showDropdownsLeft}
                      showTableRight={!this.props.data.showDropdownsRight}
                    ></SpecCompareTable>
                  ))}
              </Accordion>
            </>
          );
        }}
      </Observer>
    );
  }
}

